:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.barrier-configurator .bc__header {
  background-color: var(--text-gray);
  margin-bottom: 60px;
}

.barrier-configurator .bc__header > .bc__header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0;
}

.barrier-configurator .bc__header-logo {
  display: flex;
  align-items: center;
  gap: 8px;
}

.barrier-configurator .bc__header-menu-container {
  display: flex;
  gap: 80px;
}

.barrier-configurator .bc__header-menu-item {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.barrier-configurator .bc__header-menu-item a {
  text-decoration: none;
  color: var(--color-white);
  font-size: 20px;
  font-weight: 600;
  line-height: 120%;
  position: relative;
}

.barrier-configurator .bc__header-menu-item a::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: var(--color-white);
  transition: width 0.3s ease-out;
}

.barrier-configurator .bc__header-menu-item a:hover::after {
  width: 100%;
}

@media (max-width: 1366px) {
  .barrier-configurator .bc__header-menu-container {
    gap: 40px;
  }
}

@media (max-width: 1280px) {
  .barrier-configurator .bc__header {
    margin-bottom: 20px;
  }
  .barrier-configurator .bc__header > .bc__header-wrapper {
    padding: 14.5px 0;
  }
  .barrier-configurator .bc__header-menu-item a {
    font-size: 16px;
  }
  .barrier-configurator .bc__header-logo img {
    width: 60px;
    width: clamp(60px, 10vw, 137px);
  }
}

@media screen and (max-width: 1024px) {
  .barrier-configurator .bc__header-menu,
  .barrier-configurator .bc__header-control .bc__button {
    display: none;
  }
}
